import CONF from './config'
import Api from '@/utils/api'

const manageWaterApi = CONF.manageWater.API

export default {
  /**
   *[根据条件获取用水信息列表]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getWaterInfo(params = {}) {
    const res = await Api.get(manageWaterApi['getWaterInfo'], params)
    return res
  },
  /**
   *[用水异常判断]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async setStatus(params = {}) {
    const res = await Api.post(manageWaterApi['setStatus'], params)
    return res
  }
}
