<script>
  import Cookies from 'js-cookie'
  import Service from './service'
  import config from '@/config'

  const variationNum = '变异系数：标准差能很客观准确的反映一组数据的离散程度，但是对于不同的项目，或同一项目不同的样本，标准差就缺乏可比性了，因此引入变异系数。例如：住户A只有一个人居住是一套1居室，住户A的标准差只能反应住户A各月用水数据分布的离散程度，如果和其他住户的标准差进行对比是缺乏可比性的，例如住户B是一幢别墅，居住人数为10，各月用水量从基数量级上就远大于住户A，在标准差的绝对值上，住户B可能远大于住户A，但是住户A和住户B各月数据分布离散程度的相对值可能是相似的。简单来讲，从1到5上升了4，从10到50上升了40，后者上升的绝对值40远大于前者上升的绝对值4，但上升幅度前后两次均为80%。这也就意味着当我们对比不同项目或同一项目的不同样本时，我们也需考虑不同样本本身的数据量纲，去除样本原始数据量纲的影响，从而更客观的对比数据，变异系数=样本原始数据的标准差/样本原始数据的平均数，通过除以样本原始数据的平均数消除数据量纲的影响，表达的是样本数据离散程度的相对值'

  export default {
    name: 'ManageWater',
    data() {
      return {
        formInline: {
          houseStatus: '',//房屋状态
          houseWaterStatus: '', //状态
          // coefficientVariation: '',
          standardScore: '', //Z分数超出百分比
          houseSpecialMarkArr: [], //房屋特殊标注
          houseSpecialMark: "",
          communityName: '', //街路巷/小区名称
          suspectRent: '', //用水疑似空转租
          emptyShopping: '', //空房购物判断
          specialGoods: '', //特殊商品判断
        },
        options: [{
          value: '500',
          label: '500'
        }],
        houseSpecialMarkGroups: [{
          label: '',
          options: [{
            value: '无',
            label: '无'
          }]
          }, {
          label: '特殊标记',
          options: [{
            value: '重点人员',
            label: '重点人员'
          }, {
            value: '风险人员',
            label: '风险人员'
          }, {
            value: '涉案人员',
            label: '涉案人员'
          }]
        }],
        variationNum,
        multipleSelection: [],
        tableData: [],
        showTable: true,
        activeNames: ['1'],
        currentPage: 1,
        total: 0,
        updatedTime: '',
        isLoadingData: false,
        officerNo: '',
        fileList: [],
        dialogVisible: false,
        uploadUrl: '/water/use/uploadWaterInfoFile?uploadType=1',
        dialogJudgeAbnormalShow: false, //判定异常弹框
        judgeAbnormalInfo: {info:""}, //判定原因
        uploadTitle: "", //上传弹框标题
      }
    },
    watch: {
      currentPage() {
        this.init(this.formInline)
      }
    },
    mounted() {
      let officerNo = ''
      if (Cookies.get('officerNo')) {
        this.officerNo = Cookies.get('officerNo')
      }
      this.init()
    },
    computed: {
      isAdmin() {
        let obj = JSON.parse(Cookies.get('officerInfo'))
        return obj.role === 'admin'
      }
    },
    methods: {
      async init(params = {}) {
        // 将房屋特殊标记转化为字符串
        this.formInline.houseSpecialMark = this.formInline.houseSpecialMarkArr.join(",");
        const defaultParams = {
          currentPage: this.currentPage,
          offset: 10,
          officerNo: this.officerNo
        }
        this.isLoadingData = true
        const res = await Service.getWaterInfo({...defaultParams, ...params})
        this.isLoadingData = false
        const {data, totalSize = 0} = res
        this.tableData = data
        this.updatedTime = data[0].dt ? data[0].dt : ''
        this.total = totalSize
      },
      onQuery() {
        this.currentPage = 1
        this.init(this.formInline)
      },
      handleClick(id) {
        this.showTable = false
        this.$router.push({
          path: '/room-info',
          query: {
            proCollId: id
          }
        })
      },
      handleSelectionChange(val) {
        if (val) {
          this.multipleSelection = val.map(v => v.id)
        }
      },

      // 判断异常弹框确定
      async doDiffClick() {
        this.$refs.judgeAbnormalDialog.validate((valid) => {
          if (valid) {
            // 判定原因为空则
            const params = {
              officerNo: this.officerNo,
              ids: this.multipleSelection,
              abnormalInfo: this.judgeAbnormalInfo.info
            }
            this.addjudgeAbnormalInfo(params)
          } else {
            return false;
          }
        })
      },

      // 判断异常
      async addjudgeAbnormalInfo(params) {
        const res = await Service.setStatus(params)
        if (res.data) {
          this.$message({
            type: 'success',
            message: '判定异常成功'
          });
          this.dialogJudgeAbnormalShow = false
          this.multipleSelection = []

          this.init()
        }
      },

      /*confirmFun() {
        this.$confirm('判定原因', '异常判定确认', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doDiffClick()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
        });
      },*/
      handleSelectable(row, index) {
        return row.houseWaterStatus !== '异常'
      },
      downTable() {
        let params = {
          officerNo: this.officerNo,
          ...this.formInline
        }
        let url = config.LOCAL + '/water/use/download?' + this.dealGetParams(params)
        console.log(url)
        window.open(url)
      },
      dealGetParams(obj) {
        let arr = []
        if (obj instanceof Object) {
          Object.keys(obj).forEach(item => {
            arr.push(`${item}=${obj[item]}`)
          })
        }
        return arr.join('&')
      },
      uploadFun(obj) {
        this.dialogVisible = true;
        if (obj == 1) {
          this.uploadUrl = config.LOCAL + '/water/use/uploadWaterInfoFile?uploadType=1';
          this.uploadTitle = "上传房屋用水数据";
        }
        if (obj == 2) {
          this.uploadUrl = config.LOCAL + '/water/use/uploadHouseShoppingFile?uploadType=1';
          this.uploadTitle = "上传房屋购物数据";
        }
        if (obj == 3) {
          this.uploadUrl = config.LOCAL + '/water/use/uploadHouseShoppingFile?uploadType=1'
          this.uploadTitle = "上传房屋物流数据";
        }
      },
      handleClose() {
        this.dialogVisible = false
        this.fileList = []
      },
      onSuccess() {
        this.$message.success('文件上传成功！')
      },
      houseSpecialChange(event, item) {
        //如果选中项为无，则先清空整个选择框
        // debugger
        if (event[event.length - 1] == "无") {
          this.formInline.houseSpecialMarkArr = ["无"]
        } else if (this.formInline.houseSpecialMarkArr.indexOf("无") != -1) {
          this.formInline.houseSpecialMarkArr.splice(this.formInline.houseSpecialMarkArr.indexOf("无"), 1);
        }
      }
    }
  }
</script>

